#myabout {
    width: 100%;
    height: 100vh;

    .box {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    h1 {
        text-align: center;
        font-size: 30px;
        font-weight: 900;
    }

    h3 {
        text-align: center;
        margin-bottom: 15%;
    }

    .content {
        display: flex;

        li {
            padding: 10px;
            list-style: square;
        }
    }
}